.navbar {
  padding: 25px;

  &__box {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  &__box {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }

  &__logo {
    width: 200px;
    height: 40px;
  }

  &__tog {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;
  }

  &__menu {
    display: flex;
    flex-direction: row;
    margin: 0 15px;
    padding: 0;
    list-style: none;

    & li {
      margin: 0 15px;

      & a {
        font-family: 'Myriad Pro', sans-serif;;
        font-size: 18px;
        font-weight: 400;
        color: #585858;
        text-decoration: none;

        &:hover {
          color: #000;
        }

      }

    }

    &_ml {
      margin-left: auto;
    }

    &_mr {
      margin-right: auto;
    }

  }

}
