.download {
  padding: 45px 0;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#0079e4+0,1852d3+100 */
  background: #2daef3; /* Old browsers */
  background: -moz-linear-gradient(left, #2daef3 0%, #0171e1 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #2daef3 0%,#0171e1 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #2daef3 0%,#0171e1 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0079e4', endColorstr='#1852d3',GradientType=1 ); /* IE6-9 */

  &__row {
    display: flex;
    align-items: center;
  }

  &__col {
    width: 50%;
  }

  &__img {
    width: 250px;
    height: auto;
  }

  &__btn-box {
    width: 380px;
    margin-left: auto;
  }

  &__btn {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 15px 40px;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: normal;
    color: #fff;
    border-radius: 5px;
    text-decoration: none;

    & img {
      width: 33px;
      height: 36px;
      margin-right: 20px;
    }

    &_outline {
      border: 2px solid #fff;
    }

    &_white {
      color: #0381ea;
      background: #fff;
    }

    &_link {
      font-weight: 300;
      text-align: center;
      margin-bottom: 0;
    }

  }

  &__btn-link {
    display: block;
    margin-top: 20px;
    text-align: center;
    margin-bottom: 0;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 300;
    font-size: 24px;
    line-height: normal;
    color: #fff;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

  }

}
